	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is an affinity diagram?</h2>\n<p>An affinity diagram is used to organize information into like groups/categories to identify common themes and patterns. The creation of a nity diagrams allows the team to quickly identify trends in the data to facilitate creating accurate business and user requirements.</p>\n<h2>How to create an affinity diagram:</h2>\n<p>Start by picking out key phrases, words, and other useful parts of the interview and then organize them into like groups by writing them on sticky notes. If you are part of a remote team, you could use a free online tool like <a href=\"https://trello.com/\">Trello</a>.</p>\n<p>After the initial sticky note session is complete, organize the results into a report that can be shared. This can be done in Excel or Trello.</p>","frontmatter":{"title":"Affinity Diagram","overview":[{"heading":"Investment","amount":1,"description":"Once an interview is complete, documenting the key responses is quick and easy."},{"heading":"Validity","amount":3,"description":"A nity diagrams can be somewhat subjective, as the creator makes determinations about what was important and how these items are grouped."},{"heading":"Expertise","amount":1,"description":"Creating an a nity diagram requires no special tools or methods and can be done by anyone on the team with access to recordings of the interviews."}],"resources":null,"relatedMethods":[{"text":"Empathy Mapping","url":"/empathy-map/"},{"text":"Personas","url":"/persona/"},{"text":"Interviews","url":"/interviews/"}]}}},"pathContext":{"slug":"/affinity-diagram/"}}

